	module.exports = {"data":{"markdownRemark":{"html":"<h2>List Append Method</h2>\n<p>There are many methods for lists, you can explore all of them by typing <code>help(list)</code> in your python console.\nOne of them is the append function which, as the name says appends the argument given list.</p>\n<h4>Example Usage</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-py\"><code>words = [\"I\", \"love\", \"Python\"]\nwords.append(\"very much\")\nprint(words)</code></pre>\n      </div>\n<h4>Output</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>[\"I\", \"love\", \"Python\", \"very much\"]</code></pre>\n      </div>\n<p> As you might have noticed the element <code>\"very much\"</code> is appended to the list.</p>\n<h4>More Information:</h4>\n<p>The official documentation for <code>append()</code> can be found <a href='https://docs.python.org/3.6/tutorial/datastructures.html' target='_blank' rel='nofollow'>here</a></p>","fields":{"slug":"/python/list-append-method/"},"frontmatter":{"title":"List Append Method"}}},"pathContext":{"slug":"/python/list-append-method/"}}

